@import 'src/common/styles/mixins.scss';
@include a;
@include inputs;

.form {
  width: 70%;
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  margin-bottom: 5%;
}

.field {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .fieldName {
    font-weight: 600;
    margin-bottom: 5px;
    align-self: flex-start;
  }
}

.errorField {
  position: absolute;
  top: 65px;
  color: red;
}

.btn {
  display: block;
  width: 100px;
  height: 30px;
}